<template>
    <div id="clock">
        <p class="date">{{date}}</p>
        <p class="time">{{time}}</p>
        <p class="text">{{text}}</p>
    </div>
</template>

<script>
export default { 
    name:'Clock',
    data(){
        return{
            date:'',
            time:'',
            text:'生死看淡，不服就干'
        }  
    },
    
    methods:{
        
        updateTime(){
            var nowDate=new Date();
            var weeks=['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
            
            var hours=nowDate.getHours()<10?'0'+nowDate.getHours()+':' : nowDate.getHours()+':';
            var minutes=nowDate.getMinutes()<10?'0'+nowDate.getMinutes()+':' : nowDate.getMinutes()+':';
            var seconde=nowDate.getSeconds()<10?'0'+nowDate.getSeconds() : nowDate.getSeconds();
            var year=nowDate.getFullYear()<10?'0'+nowDate.getFullYear()+'-' : nowDate.getFullYear()+'-';
            var month=nowDate.getMonth()+1<10?'0'+(nowDate.getMonth()+1)+'-' : (nowDate.getMonth()+1)+'-'
            var day=nowDate.getDate()<10?'0'+nowDate.getDate() : nowDate.getDate();

            this.date=year+month+day+'    '+weeks[nowDate.getDay()];
            this.time=hours+minutes+seconde;          
        }
        
    },
    created(){
      setInterval(this.updateTime,1000)
    }
}
</script>

<style scoped>

#clock {
  
  font-family: 'Share Tech Mono', monospace;
  color: #ffffff;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #daf6ff;
  text-shadow: 0 0 20px #0aafe6, 0 0 20px rgba(10, 175, 230, 0);
}
#clock .time {
  letter-spacing: 0.05em;
  font-size: 80px;
  padding: 5px 0;
}
#clock .date {
  letter-spacing: 0.1em;
  font-size: 24px;
}
#clock .text {
  letter-spacing: 0.1em;
  font-size: 20px;
  padding: 20px 0 0;
}

</style>